         identifyid:[
           {required:true,validator: this.validate.identifyidRule,message: '(有误)', trigger: 'blur' },
//           {required: false,validator: this.validate.identifyidRule,message: '(有误)', trigger: 'submit' }
         ],
         contact_phone:[
           {required: true,validator: this.validate.validatetel,message: '(必填)', trigger: 'blur' },
           {required: true,validator: this.validate.validatetel,message: '(必填)', trigger: 'submit' }
         ]
       },
       gender:'male',
      sos:'Y',
       age:'1',
      car:'京',
      shouhuan:'2',
      patient:[],
      // carTime:'',//发车时间
      // arrivalTime:"",//到达时间
      // firstContactMedicalTime:'',//首次接触医疗时间
      // drawBloodTime:"",//抽血时间
      // firstTakeMedicineTime:''//首次吃药时间
    };
  },
  created() {
    this.codeGet();
    this.GetBaseValue();
    this.isHelpDate();
//   console.log('基本页面')
  },
  mounted() {
      // this.patientinfo=localStorage.getItem('patientinfo')
  },
  methods: {
    //根据身份证计算出出生日期及年龄
    identifyDate(ev){
      if(ev.target.value && ev.target.value.length==18){
        var datestr=ev.target.value.substr(6,8);
        let yearstr=datestr.substr(0,4);
        let monthstr=datestr.substr(4,2);
        let daystr=datestr.substr(6,2);
        let result=yearstr+"-"+monthstr+"-"+daystr;
        this.patientinfo.padPatInfoView.birthday=result;
        this.getAge(this.patientinfo.padPatInfoView.birthday)
      }
    },
    //出生日期获取年龄
    getAge(ev){
      var pThis=this;
      if(ev==""){return;}
      pThis.$http.post(pThis.host('task/getage'),{birthday:ev})
        .then(function (res) {
          let datajson = res.data;
          let backjson={"state":datajson.state,"msg":datajson.msg};
          pThis.backLogin(backjson);
          if(datajson.state=='1'){
            // debugger
            var age=parseInt(datajson.results[0].age);
            var ageUnit=datajson.results[0].age.substr(datajson.results[0].age.length-1,1) ;
            // console.log(ageUnit);
            for(var a=0;a<pThis.ageList.length;a++){
              if(pThis.ageList[a].name==ageUnit){
                pThis.patientinfo.padPatInfoView.age_value=age;
                pThis.patientinfo.padPatInfoView.age_unit=pThis.ageList[a].code;
                // Vue.set(pThis.addPatients,'age_value',age);
                // Vue.set(pThis.addPatients,'age_unit',pThis.ageUnitArr[a].code);
              }
            }
          }
        }).catch(function (error) {
        console.log(error)
      });
    },
    //获取焦点事件
    scrollChange(ev){
      // console.log(ev)
      var div = document.getElementById("baseInfo");
//      if (div.scrollHeight) {
        // var height = div.scrollHeight;
        // var height = ev.srcElement.offsetHeight;
        // var scrolltop=$("#baseInfo").scrollTop();
        // var divHeight=div.offsetTop;
        // var inputHeight=$(ev.target).offset().top;
        // var height=parseInt(scrolltop)+parseInt(divHeight)+40;
       // //  console.log(height)
       // //  // $("#baseInfo").scrollTop(height);
       setTimeout(function () {
         $("#baseInfo").scrollTop(ev*40)
       }, 500)
//      }
    },
    scrollChange2(ev){
      var div = document.getElementById("baseInfo");
      if (div.scrollHeight) {
        // var height = div.scrollHeight;
        var height =this.offsetHeight;
        // $("#baseInfo").scrollTop(height);
        setTimeout(function () {
          $("#baseInfo").scrollTop(height)
        }, 300)
      }
    },
        isHelpDate(){
            if(this.baseInfo.is_help =='1'){
                this.isHelp = false;
            }else{
              this.isHelp = true;
            }
        },
    //身份证号校验
    idRule(ev,json){
      var re = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;//判断字符串是否为数字//判断正整数/[1−9]+[0−9]∗]∗/
      if(ev.target.value==""){
        return callback();
      }else if (!re.test(ev.target.value)) {
        return callback(new Error("有误!"));
      } else{
        callback();
      }
    },
    //数字位校验
    Rule(ev,json,num,rule){
      if (ev.keyCode == 37 || ev.keyCode == 39) {
        return;
      }else {
        if(!num){
          num=3
        }
        if(!rule){
//            rule=/[^\d]/g
//            rule=/[^\u4e00-\u9fa5]{0,}$/
          rule=''
        }
//          ev.target.value = ev.target.value.replace(/[^\-?\d.]/g,'');
        ev.target.value = this.validate.formatNum(rule,num, ev)
        Vue.set(this.patientinfo[json[0]], json[1], ev.target.value)
      }
    },
    //手机号校验
    mobileRule(ev,json,num,rule){
      if (ev.keyCode == 37 || ev.keyCode == 39) {
        return;
      }else {
        if(!num){
          num=3
        }
        if(!rule){
          rule=/[^\d]/g
        }
        ev.target.value = this.validate.formatNum(rule,num, ev)
        Vue.set(this.patientinfo[json[0]], json[1], ev.target.value)
      }
    },
    timeChange(event,value){
        var pThis = this;
//      if(typeof date =='object'){
        var date = new Date(event);
        var year=date.getFullYear();
        var month=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
        var day=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
        var hours=date.getHours() <=9 ?"0"+date.getHours():date.getHours();
        var mins=date.getMinutes() <=9 ?"0"+date.getMinutes():date.getMinutes();
        var str =  year+"-"+month+"-"+day+" "+hours+":"+mins;
      console.log(str)
        pThis.patientinfo.padTimeView[value]= str;
    },
    CheckChange(value){   //全局变量值交换
        var pThis = this;
      pThis.patientinfo.padPatInfoView[value]= pThis.baseInfo[value];
      if(value = 'is_help'){
          this.isHelp = !this.isHelp;
      }
    },
    GetBaseValue(){   //全局变量赋值
      var pThis = this;
      if(pThis.patientinfo.padPatInfoView){
        for(var value in pThis.patientinfo.padPatInfoView){
          pThis.baseInfo[value] = pThis.patientinfo.padPatInfoView[value];
        }
      }
    },
    codeGet(){  //字典查询
         var pThis = this;
         var codes = {code:["AGEUNITS","CARFOLLOWING","AMBULANCE","HAND_RING_CODE","BOX_CODE"]};//年龄单位 随车人员 车辆 手环编号 实时监控盒子编号
      this.$http.post(pThis.host('defdoc/getDefdocViewByCodes'),codes)
        .then(function (res) {
          let datajson = res.data;
          let backjson={"state":datajson.state,"msg":datajson.msg};
          pThis.backLogin(backjson);
          if(datajson.state=='1'){
            var data=datajson.results[0];
            for(var key in data){
              if(key=='AGEUNITS'){//年龄单位
                pThis.ageList=data[key];
                console.log(pThis.ageList)
              }
              if(key=='CARFOLLOWING'){//随车人员
                pThis.peopleList=data[key];
              }
              if(key=='AMBULANCE'){//车辆
                pThis.carList=data[key];
              }
              if(key=='HAND_RING_CODE'){//手环编号
                pThis.handDodeList=data[key];
              }
              if(key=='BOX_CODE'){//实时监控盒子编号
                pThis.boxCodeList=data[key];
              }
            }
          }
        }).catch(function (error) {
        console.log(error)
      });
    },
    cancelPro() {
      this.$refs.cancelModal.openmodal();
      // this.$refs.tipmodal1.openmodal(); //点击取消项目打开弹框提示：是否要删除
    },
    agreeCancel() {
      // this.$refs.cancelModal.openmodal();
      this.$router.push({ path: "home" });
    },
    apply() {
      this.$refs.agreeModal.showModal(); //点击取消项目打开弹框提示：是否要删除
    },
    ok() {
      this.$refs.agreeModal2.showModal();
    },
    cancel() {
      this.$refs.agreeModal2.cancel();
    },
    twiceOk() {
      // this.$refs.agreeModal2.openmodal();
      this.noApply = true;
    },
  }
}
</script>
